import React, { Component } from 'react'
import $ from "jquery";

export default class LanguageBoard extends Component {
	constructor(props) {
		super(props);

		this.press = this.pressEN.bind(this);
		this.press = this.pressUA.bind(this);
		this.press = this.pressRU.bind(this);
	}
	pressEN() {
		if (!$('#en').hasClass('active')) {
			$('#en').addClass('active');
			$('#ua').removeClass('active');
			$('#ru').removeClass('active');
			lightShock();
			// setTimeout(changeLanguage, 150, 'stringEN.xml');
		}
	}
	pressUA() {
		if (!$('#ua').hasClass('active')) {
			$('#en').removeClass('active');
			$('#ua').addClass('active');
			$('#ru').removeClass('active');
			lightShock();
			// setTimeout(changeLanguage, 150, 'stringUA.xml');
		}
	}
	pressRU() {
		if (!$('#ru').hasClass('active')) {
			$('#en').removeClass('active');
			$('#ua').removeClass('active');
			$('#ru').addClass('active');
			lightShock();
			// setTimeout(changeLanguage, 150, 'stringRU.xml');
		}
	}
	render() {
		return (
			<div id="language-board">
				<div id="language-elements">
					<p id="en" className="active" onClick={this.pressEN}>English</p>
					<p id="ua" onClick={this.pressUA}>Українська</p>
					<p id="ru" onClick={this.pressRU}>Русский</p>
				</div>
			</div>

		)
	}
}

function lightShock() {
	$('#light-shock').css('display', 'block');
	$('#light-shock').css('animation', 'lightshock .5s ease');
	setTimeout(function () {
		$('#light-shock').css('display', 'none');
	}, 500);
}


