import React, { Component } from 'react'
import './SoftSkilsPaper.css';
import CloseButton from './CloseButton'

export default class SoftSkilsPaper extends Component {
    // constructor(props) {
    //     super(props);
    // }

    componentDidMount() {
        const script = document.createElement("script");

        script.src = "/wheelSoftSkills.js";
        script.async = false;
        document.body.appendChild(script);
    }
    render() {
        return (
            <div id="soft-skills-block" className="layer-content">
                <div id="title">
                    <h2 id="s6">SOFT-SKILLS</h2>
                    <hr />
                </div>
                <div id="soft-skills-content">
                    <div id="soft-skills-wheel">
                        <canvas id="myCanvas" width="300" height="300">
                            Ваш браузер не поддерживает Canvas
        			</canvas>
                        <p>Acting as a <br />team player</p>
                        <p>Flexibility</p>
                        <p>Effective <br />communication</p>
                        <p>Problem-solving <br />and resourcefulness</p>
                        <p>Accepting <br />feedback</p>
                        <p>Confidence <br />is key</p>
                        <p>Creative<br /> thinking</p>
                        <p>Other</p>
                    </div>

                </div>

                <CloseButton />


            </div>
        )
    }
}
