import React from 'react'
import './SkillsPaper.css';
import CloseButton from './CloseButton'
export default function SkillsPaper() {
    return (
        <div id="technical-skills-block" className="layer-content">
            <div id="title">
                <h2 id="s6">TECHNICAL SKILLS</h2>
                <hr></hr>
            </div>

            <CloseButton />

            <div id="technical-skills-content">
                <div id="front">
                    <h3>Frontend</h3>
                    <hr></hr>
                    <ul className="skills-list">
                        <li>
                            <p>HTML5</p>
                            <div id="html5" className="progress">
                                <div className="fill"></div>
                                <div className="number">60%</div>
                            </div>
                        </li>
                        <li>
                            <p>CSS3</p>
                            <div id="css3" className="progress">
                                <div className="fill"></div>
                                <div className="number">50%</div>
                            </div>
                        </li>
                        <li>
                            <p>JavaScript</p>
                            <div id="js" className="progress">
                                <div className="fill"></div>
                                <div className="number">30%</div>
                            </div>
                        </li>
                        <li>
                            <p>JQuery</p>
                            <div id="jquery" className="progress">
                                <div className="fill"></div>
                                <div className="number">20%</div>
                            </div>
                        </li>
                    </ul>

                </div>
                <div id="back">
                    <h3>Backend</h3>
                    <hr></hr>
                    <ul className="skills-list">
                        <li>
                            <p>Java Core</p>
                            <div id="java" className="progress">
                                <div className="fill"></div>
                                <div className="number">70%</div>
                            </div>
                        </li>
                        <li>
                            <p>Python</p>
                            <div id="python" className="progress">
                                <div className="fill"></div>
                                <div className="number">30%</div>
                            </div>
                        </li>
                        <li>
                            <p>SQL</p>
                            <div id="sql" className="progress">
                                <div className="fill"></div>
                                <div className="number">40%</div>
                            </div>
                        </li>
                    </ul>
                </div>
                <div id="other">
                    <h3>Other</h3>
                    <hr></hr>

                </div>
            </div>

        </div>
    )
}
