import React, { Component } from 'react'
import $ from "jquery"

export default class MainBoard extends Component {
	constructor(props) {
		super(props);

		this.press = this.pressPersonalPaper.bind(this);
		this.press = this.pressSkillsPaper.bind(this);
		this.press = this.pressSoftSkilsPaper.bind(this);
		this.press = this.pressLanguagePaper.bind(this);
		this.press = this.pressPortfolioPaper.bind(this);
	}
	pressPersonalPaper() {
		$('#personal-information-block').css('display', 'block');
		$('#personal-information-block').css('animation', 'blockappear 1s ease');
	}
	pressSkillsPaper() {
		$('#technical-skills-block').css('display', 'block');
		$('#technical-skills-block').css('animation', 'blockappear 1s ease');
	}
	pressSoftSkilsPaper() {
		$('#soft-skills-block').css('display', 'block');
		$('#soft-skills-block').css('animation', 'blockappear 1s ease');
	}
	pressLanguagePaper() {
		$('#language-skills-block').css('display', 'block');
		$('#language-skills-block').css('animation', 'blockappear 1s ease');
	}
	pressPortfolioPaper() {
		window.open('works/works.html', '_blank');
	}
	render() {
		return (
			<div id="main-board-menu">
				<div id="container">
					<div id="personal-paper"><p id="s1" onClick={this.pressPersonalPaper}>PERSONAL INFORMATION</p></div>
					<div id="skills-paper"><p id="s2" onClick={this.pressSkillsPaper}>TECHNICAL <br></br>SKILLS</p></div>
					<div id="soft-skills-paper"><p id="s3" onClick={this.pressSoftSkilsPaper}>SOFT-SKILLS</p></div>
					<div id="language-paper"><p id="s4" onClick={this.pressLanguagePaper}>FOREIGN LANGUAGES</p></div>
					<div id="portfolio-paper"><p id="s5" onClick={this.pressPortfolioPaper}>MY WORKS</p></div>
				</div>
			</div>
		)
	}
}

