import React, { Component } from 'react'
import ReactDOM from "react-dom";
import * as THREE from 'three';
// import OrbitControls from 'three-orbitcontrols';
import './styleBg.css';

export default class Bg3D extends Component {
    componentDidMount() {


        let scene, camera, renderer;
        let skybox;
        let i = 0;
        let responsivWindow;
        const container = document.getElementById('Bg3D-block');

        function init() {
            scene = new THREE.Scene();
            camera = new THREE.PerspectiveCamera(80, window.innerWidth / window.innerHeight, 55, 30000);
            camera.position.set(-900, 1200, 900);

            renderer = new THREE.WebGLRenderer({ antialias: true });
            renderer.setSize(window.innerWidth, window.innerHeight);
            document.body.appendChild(renderer.domElement);


            var Ground_geometry = new THREE.BoxGeometry(2500, 0.1, 1000);
            let texture_grass = new THREE.TextureLoader().load(require('./tex/gras4.jfif'));
            texture_grass.wrapS = texture_grass.wrapT = THREE.RepeatWrapping;
            texture_grass.repeat.set(1, 1);
            texture_grass.anisotropy = 160;
            // texture_grass.encoding = THREE.sRGBEncoding;
            var Ground_material = new THREE.MeshBasicMaterial({ map: texture_grass });


            var ground = new THREE.Mesh(Ground_geometry, Ground_material);
            ground.position.set(-900, 300, 500);
            ground.scale.multiplyScalar(3);
            ground.castShadow = true;
            ground.receiveShadow = true;
            scene.add(ground);



            // let controls = new OrbitControls(camera, renderer.domElement);
            // controls.addEventListener('change', renderer);
            // controls.minDistance = 500;
            // controls.maxDistance = 1500;
            let dayTime = 'day';
            let texture_ft;
            let texture_bk;
            let texture_up;
            let texture_dn;
            let texture_rt;
            let texture_lf;


            let materialArray = [];
            switch (dayTime) {
                case 'sunset':
                    texture_ft = new THREE.TextureLoader().load(require('./tex/sunset/front.png'));
                    texture_bk = new THREE.TextureLoader().load(require('./tex/sunset/back.png'));
                    texture_up = new THREE.TextureLoader().load(require('./tex/sunset/up.png'));
                    texture_dn = new THREE.TextureLoader().load(require('./tex/sunset/down.png'));
                    texture_rt = new THREE.TextureLoader().load(require('./tex/sunset/right.png'));
                    texture_lf = new THREE.TextureLoader().load(require('./tex/sunset/left.png'));
                    break;

                default:
                    texture_ft = new THREE.TextureLoader().load(require('./tex/day/front.png'));
                    texture_bk = new THREE.TextureLoader().load(require('./tex/day/back.png'));
                    texture_up = new THREE.TextureLoader().load(require('./tex/day/up.png'));
                    texture_dn = new THREE.TextureLoader().load(require('./tex/day/down.png'));
                    texture_rt = new THREE.TextureLoader().load(require('./tex/day/right.png'));
                    texture_lf = new THREE.TextureLoader().load(require('./tex/day/left.png'));
                    break;
            }
            // let texture_ft = new THREE.TextureLoader().load(require('./tex/sunset/front.png'));
            // let texture_bk = new THREE.TextureLoader().load(require('./tex/sunset/back.png'));
            // let texture_up = new THREE.TextureLoader().load(require('./tex/sunset/up.png'));
            // let texture_dn = new THREE.TextureLoader().load(require('./tex/sunset/down.png'));
            // let texture_rt = new THREE.TextureLoader().load(require('./tex/sunset/right.png'));
            // let texture_lf = new THREE.TextureLoader().load(require('./tex/sunset/left.png'));

            materialArray.push(new THREE.MeshBasicMaterial({ map: texture_ft }));
            materialArray.push(new THREE.MeshBasicMaterial({ map: texture_bk }));
            materialArray.push(new THREE.MeshBasicMaterial({ map: texture_up }));
            materialArray.push(new THREE.MeshBasicMaterial({ map: texture_dn }));
            materialArray.push(new THREE.MeshBasicMaterial({ map: texture_rt }));
            materialArray.push(new THREE.MeshBasicMaterial({ map: texture_lf }));
            for (let i = 0; i < 6; i++)
                materialArray[i].side = THREE.BackSide;
            let skyboxGeo = new THREE.BoxGeometry(10000, 20000, 10000);
            skybox = new THREE.Mesh(skyboxGeo, materialArray);

            scene.add(skybox);

            container.appendChild(renderer.domElement);
            animate();
        }
        function animate() {
            renderer.render(scene, camera);
            skybox.rotation.set(0, i += 0.0004, 0, 'XYZ');

            // renderer.setSize(window.innerWidth, window.innerHeight);
            requestAnimationFrame(animate);
        }
        init();
    }
    render() {
        return (
            <div id="Bg3D-block">

            </div>
        )
    }
}

