import React, { Component } from 'react'
import $ from "jquery"

export default class CloseButton extends Component {
    constructor(props) {
        super(props);

        this.press = this.CloseButton.bind(this);
    }
    CloseButton() {
        $('.close-button').parent().css('animation', 'blockhide .5s ease');
        setTimeout(function () {
            $('.close-button').parent().css('display', 'none');
        }, 500);
    }
    render() {
        return (
            <div className="close-button" onClick={this.CloseButton}></div>
        )
    }
}
